import React, { Component } from "react";
import { Button, ButtonGroup, Col, Row } from "react-bootstrap";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import { observer, inject } from "mobx-react";

import AddRemoveButtonGroup from "./AddRemoveButtonGroup";
import icongroup from "./img/outline-add_comment-24px.svg";

const Float2DigitsFormatter = (cell, _row) => (
  <span>{Number(cell).toFixed(2)}</span>
);

const BoundaryTypeFormatter = (cell, _row) => (
  <span>{boundarytypesmap[cell]}</span>
);

const boundarytypesmap = {
  EXTERIOR: "EXTERIOR",
  INTERIOR: "INTERIOR",
  ADIABATIC: "ADIABTICO",
  UNDERGROUND: "TERRENO",
};

const boundarytypes = [
  { text: "EXTERIOR", value: "EXTERIOR" },
  { text: "INTERIOR", value: "INTERIOR" },
  { text: "ADIABTICO", value: "ADIABATIC" },
  { text: "TERRENO", value: "UNDERGROUND" },
];

const OpacosTable = inject("appstate")(
  observer(
    class OpacosTable extends Component {
      constructor(props, context) {
        super(props, context);
        this.state = { selected: [] };
      }

      onRowSelect(row, isSelected) {
        const name = row.name;
        if (isSelected) {
          this.setState({ selected: [...this.state.selected, name] });
        } else {
          this.setState({
            selected: this.state.selected.filter((it) => it !== name),
          });
        }
      }

      render() {
        const { Co100, envelope, opacosA, opacosAU } = this.props.appstate;
        const walls = envelope.walls;

        return (
          <Col>
            <Row>
              <Col>
                <h4>Elementos opacos de la envolvente trmica</h4>
              </Col>
              <Col md="auto">
                <ButtonGroup>
                  <Button
                    variant="default"
                    size="sm"
                    title="Agrupa opacos, sumando reas de igual transmitancia y factor de ajuste."
                    onClick={() => this.props.appstate.agrupaOpacos()}
                  >
                    <img src={icongroup} alt="Agrupar opacos" /> Agrupar opacos
                  </Button>
                </ButtonGroup>
              </Col>
              <Col md="auto">
                <AddRemoveButtonGroup
                  objects={walls}
                  newObj={this.props.appstate.newOpaco}
                  selected={this.state.selected}
                  clear={() => this.setState({ selected: [] })}
                />
              </Col>
            </Row>
            <Row>
              <Col
                title="Coeficiente de caudal de aire de la parte opaca de la envolvente
                trmica (a 100 Pa)"
                className="my-3 text-right"
              >
                <b>
                  C<sub>o</sub>
                </b>{" "}
                ={" "}
                <input
                  type="text"
                  onChange={(e) => {
                    this.props.appstate.Co100 = Number(e.target.value);
                  }}
                  value={Co100}
                />{" "}
                m<sup>3</sup>/hm<sup>2</sup>
              </Col>
            </Row>
            <Row>
              <Col>
                <BootstrapTable
                  data={walls}
                  version="4"
                  striped
                  hover
                  bordered={false}
                  cellEdit={{ mode: "dbclick", blurToSave: true }}
                  selectRow={{
                    mode: "checkbox",
                    clickToSelectAndEditCell: true,
                    selected: this.state.selected,
                    onSelect: this.onRowSelect.bind(this),
                    hideSelectColumn: true,
                    bgColor: "lightgray",
                  }}
                >
                  <TableHeaderColumn
                    dataField="name"
                    isKey={true}
                    headerText="Nombre que identifica de forma nica el elemento opaco"
                    width="30%"
                  >
                    Nombre
                  </TableHeaderColumn>
                  <TableHeaderColumn
                    dataField="A"
                    dataFormat={Float2DigitsFormatter}
                    headerText="rea del elemento opaco (m)"
                  >
                    A<br />
                    <span style={{ fontWeight: "normal" }}>
                      <i>
                        [m<sup>2</sup>]
                      </i>{" "}
                    </span>
                  </TableHeaderColumn>
                  <TableHeaderColumn
                    dataField="U"
                    dataFormat={Float2DigitsFormatter}
                    headerText="Transmitancia trmica del elemento opaco (W/mK)"
                  >
                    U<br />
                    <span style={{ fontWeight: "normal" }}>
                      <i>
                        [W/m<sup>2</sup>K]
                      </i>{" "}
                    </span>
                  </TableHeaderColumn>
                  <TableHeaderColumn
                    dataField="bounds"
                    editable={{
                      type: "select",
                      options: { values: boundarytypes },
                    }}
                    dataFormat={BoundaryTypeFormatter}
                    headerText="Condicin de contorno del elemento opaco (INTERIOR | EXTERIOR | UNDERGROUND | ADIABATIC)"
                  >
                    Tipo
                    <br />
                    <span style={{ fontWeight: "normal" }}>
                      <i>[-]</i>{" "}
                    </span>
                  </TableHeaderColumn>
                </BootstrapTable>
              </Col>
            </Row>
            <Row>
              <Col>
                &sum;b<sub>tr,x</sub>A<sub>x</sub> = {opacosA.toFixed(2)} m
              </Col>
            </Row>
            <Row>
              <Col md="auto">
                &sum;b<sub>tr,x</sub>&sum;<sub>i</sub>A<sub>i</sub>U
                <sub>i</sub> = {opacosAU.toFixed(2)} W/K
              </Col>
            </Row>
            <Row className="text-info small mt-3">
              <Col>
                <p>Donde:</p>
                <ul>
                  <li>
                    <b>A</b>: rea del elemento opaco (m)
                  </li>
                  <li>
                    <b>U</b>: transmitancia trmica del elemento opaco (W/mK)
                  </li>
                  <li>
                    <b>Tipo</b>: Condicin de contorno del elemento opaco
                    (EXTERIOR, INTERIOR, ADIABTICO, TERRENO). Determina el
                    factor de ajuste del elemento opaco (b<sub>tr,x</sub>), que
                    vale 1 para elementos en contacto con el aire exterior o el
                    terreno y 0 para el resto (adiabticos y en contacto con
                    otros espacios).
                    <p>
                      Esta simplificacin introduce cierto error al considerar
                      que el intercambio de calor a travs de los elementos en
                      contacto con otros edificios o espacios adyacentes es
                      despreciable, pero simplifica considerablemente los
                      clculos y el objetivo del parmetro K no es, en el caso
                      del DB-HE, el clculo de la demanda energtica si no como
                      indicador de la calidad de la envolvente trmica.
                    </p>
                  </li>
                </ul>
              </Col>
            </Row>
          </Col>
        );
      }
    }
  )
);

export default OpacosTable;
