import React, { Component } from "react";
import {
  Alert,
  Button,
  Col,
  Container,
  Row,
  Image,
  Tabs,
  Tab,
} from "react-bootstrap";

import Footer from "./Footer";
import NavBar from "./Nav";

import esquema5R1C from "./img/5R1C.svg";
